.product {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    z-index: 1;

    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    transition-duration: 1s;

    /* min-width: 100px; */
    max-width: 500px;

}

.product:hover {
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.321);
    transform: scale(1.02);
}

.product__rating {
    display: flex;
}

.product > img {
    max-height: 240px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__info {
    width: 100%;
    height: 100px;
    margin-bottom: 15px;

}

.product > button {
    background-color: #b6b6b6;
    /* border: 1px solid; */
    border-style: none;
    margin-top: 10px;
    padding: 5px 20px;
    border-radius: 3px;
    color: #111;
    /* border: #a88734 #9c7e31 #846a29; */
    border: 1px solid black;
    cursor: pointer;
    transition-duration: 0.5s;
    font-size: 15px;
}

.product > button:hover {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.300);
    background-color: #2e2e2e;
    transform: scale(1.15);
    color: white;
}

.product__price {
    margin-top: 10px;
}
