@media screen and (max-width: 390px) {
    .subtotal {
        min-width: 100px;

    }
}

@media screen and (min-width: 650px) {
    .subtotal > p {
        font-size: 25px;
    }

    .subtotal > button {
        font-size: 15px;
        /* padding: 10px; */
        /* box-sizing: border-box; */
    }
}


.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* width: 300px; */
    height: 100px;
    padding: 20px;
    background-color: #dfdfdf;
    border: 1px solid #b3b3b3;
    /* border: 10px solid black; */
    border-radius: 3px;
}

.subtotal__gift {
    display: flex;
    align-items: center;
}

.subtotal__gift > input {
    margin-right: 5px;
}

.subtotal > button {
    background-color: #b6b6b6;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    transition: 0.5s;
    cursor: pointer;
}

.subtotal > button:hover {
    color: white;
    background-color: #2e2e2e;
    
}