@media screen and (max-width: 310px) {
    .header { 
        display: flex;
        flex-direction: column;
        /* background-color: black; */
        height: 120px !important;
        justify-content: center;
        align-items: center;
    }
  }

.header {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* background-color: rgb(189, 189, 189); */
    background-color: rgb(238, 232, 226);
    position: sticky;
    z-index: 100;
    top: 0;

}

.header__logo {
    /* box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.377); */
    width: 150px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 5px;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
    padding: 10px;
    
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: rgb(22, 22, 22);
}

.header__optionLineOne {
    font-size: 10px;
}

.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
    color: rgb(22, 22, 22);
}

.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;

}