.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 150px;
    margin-right: auto;
    margin-left: auto;
}

.login__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login__container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login__signInButton {
    /* background: #f0c14b; */
    background-color: #b6b6b6;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    /* border-color: #a88734 #9c7e31 #846a29; */
    border: 1px solid black;
    font-size: 15px;
    transition: 0.5s;
    cursor: pointer;
}

.login__signInButton:hover {
    background-color: #2b2b2b;
    color: white;
}

.login__registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px; 
    /* border-color: darkgray; */
    background-color: #2b2b2b;
    color: white;
    cursor: pointer;
    transition: 0.5s;
}

.login__registerButton:hover {
    background-color: #b6b6b6;
    color: black;
}

form > h5 {
    font-size: 16px;
}